                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="isInvolveThird"
                    label="涉及第三方"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入涉及第三方' }]"
                  >
                    <DictSelectBox
                      :type="`yes_or_no`"
                      @change="`()=> detailFormState.isInvolveThird = value`"
                      v-model:value="detailFormState.isInvolveThird"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
