                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="thirdPartyName"
                    label="第三方名称"
                    :labelCol="{ span: 6 }"
                    :rules="[{ message: '请输入第三方名称' }]"
                    :required="detailFormState.isInvolveThird == 1"
                  >
                    <a-input
                      v-model:value="detailFormState.thirdPartyName"
                      placeholder="请输入第三方名称"
                      :disabled="detailFormState.status != 0 || detailFormState.isInvolveThird == 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="isInsuranceClaims"
                    label="保险理赔"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入保险理赔' }]"
                  >
                    <DictSelectBox
                      :type="`yes_or_no`"
                      @change="`()=> detailFormState.isInsuranceClaims = value`"
                      v-model:value="detailFormState.isInsuranceClaims"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="isInsured"
                    label="已报保险"
                    :labelCol="{ span: 6 }"
                    :rules="[
                      {
                        required: detailFormState.isInsuranceClaims == 1,
                        message: '请选择已报保险',
                      },
                    ]"
                  >
                    <DictSelectBox
                      :type="`yes_or_no`"
                      @change="`()=> detailFormState.isInsured = value`"
                      v-model:value="detailFormState.isInsured"
                      style="width: 100%"
                      :disabled="
                        detailFormState.status != 0 || detailFormState.isInsuranceClaims == 0
                      "
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="casualties"
                    label="人员伤亡"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入人员伤亡' }]"
                  >
                    <DictSelectBox
                      :type="`Casualties`"
                      @change="`()=> detailFormState.casualties = value`"
                      v-model:value="detailFormState.casualties"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="casualtiesTotal"
                    label="伤亡人数"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入伤亡人数' }]"
                  >
                    <a-input-number
                      v-model:value="detailFormState.casualtiesTotal"
                      placeholder="请输入伤亡人数"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="damage"
                    label="设备损坏情况"
                    :labelCol="{ span: 3 }"
                    :rules="[{ required: true, message: '请输入遗留整改计划' }]"
                  >
                    <a-textarea
                      v-model:value="detailFormState.damage"
                      row="8"
                      show-count
                      style="height: 200px"
                      :disabled="disabledInput"
                      :maxlength="1000"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="isPowerFailure"
                    label="停电"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入停电' }]"
                  >
                    <DictSelectBox
                      :type="`yes_or_no`"
                      @change="`()=> detailFormState.isPowerFailure = value`"
                      v-model:value="detailFormState.isPowerFailure"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="blackoutRange"
                    label="停电范围"
                    :labelCol="{ span: 6 }"
                    :rules="[{ message: '请输入停电范围' }]"
                    :required="detailFormState.isPowerFailure == 1"
                  >
                    <a-input
                      v-model:value="detailFormState.blackoutRange"
                      placeholder="请输入停电范围"
                      :disabled="detailFormState.status != 0 || detailFormState.isPowerFailure == 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="equipment"
                    label="停电设备"
                    :labelCol="{ span: 3 }"
                    :rules="[{ message: '请输入停电设备' }]"
                    :required="detailFormState.isPowerFailure == 1"
                  >
                    <a-textarea
                      v-model:value="detailFormState.equipment"
                      row="6"
                      show-count
                      style="height: 200px"
                      :disabled="detailFormState.status != 0 || detailFormState.isPowerFailure == 0"
                      :maxlength="1000"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="content"
                    label=" 快报正文"
                    :labelCol="{ span: 3 }"
                    :rules="[{ required: true, message: '请输入快报正文' }]"
                  >
                    <a-textarea
                      v-model:value="detailFormState.content"
                      row="8"
                      show-count
                      style="height: 300px"
                      :disabled="disabledInput"
                      :maxlength="1000"
                    />
                  </a-form-item>
                </a-col>
              </a-row>
            </a-card>
            <a-card title="上传附件" :bordered="false">
              <div style="margin: 5px 16px 20px 10px">
                <UploadTable
                  ref="uploadTable"
                  :rows="uploadRows"
                  :colwidths="['15%', '70%', '15%']"
                  :application="`po`"
                  :module="`safetyAccident`"
                  :mode="detailFormState.status == 0 ? `normal` : `view`"
                />
              </div>
            </a-card>
          </a-form>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script lang="ts" setup>
  import { reactive, ref, onMounted, unref } from 'vue';
  import { saveData, initInfoItems, getIndex } from './safetyAccident';
  import { FormInstance, message } from 'ant-design-vue';
  import { useRouter } from 'vue-router';
  import WfApproveBox from '/@/components/Framework/WorkFlow/WfApproveBox.vue';
  import { assign } from 'lodash-es';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import BillTitle from '/@/components/Framework/BillTitle/BillTitle.vue';
  import { BasicTable, useTable, EditRecordRow, BasicColumn } from '/@/components/Table';
  import UploadTable from '/@/components/Framework/Table/UploadTable.vue';
  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';

  import * as FileApi from '@/api/infra/file';
  import UploadBox from '@/components/Framework/Combox/UploadBox.vue';
  import { closeCurrentTab } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import { useUserStore } from '/@/store/modules/user';
  import { Memory } from '@/router/index';

  const uploadTable = ref();
  const uploadRows = ref<any[]>([]);
  const detailFormState = reactive({ spinning: false });
  const userStore = useUserStore();
  const getUserInfo = userStore.getUserInfo;
  const disabledInput = ref<boolean>(true);
  const billTitleOption = reactive<any>({});
  billTitleOption.subject = '安全事故快报';
  billTitleOption.infoItems = [];
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const query = Memory.getInstance().getQuery(); // route.query;

  const formRef = ref<FormInstance>();

  function save(status) {
    detailFormState.spinning = true;
    try {
      status = status || 0;
      formRef.value.validateFields().then(() => {
        detailFormState.status = status;
        saveData(detailFormState).then((data) => {
          detailFormState.id = data;
          getIndex({ id: data }).then((data) => {
            assign(detailFormState, data.result);
          });
          message.success(status == 1 ? '提交成功。' : '保存成功');
          MsgManager.getInstance().sendMsg('safetyAccident', {});
          if (status == 1) {
            disabledInput.value = true;
            closeCurrentTab();
          }
        });
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      detailFormState.spinning = false;
    }
  }

  function onSubmit() {
    save(1);
  }

  function onSave() {
    save(0);
  }
  assign(detailFormState, {
    processInstanceId: '',
    id: '',
    billCode: '',
    fillinDate: '',
    organId: '',
    organName: '',
    deptId: '',
    deptName: '',
    positionId: '',
    positionName: '',
    personMemberId: '',
    personMemberName: '',
    fullId: '',
    status: 0,
    bizFileId: '',
    subject: '',
    finishedDate: '',
    name: '',
    accidentDate: '',
    accidentLevel: '',
    isReportGovernment: '',
    isInvolveThird: '',
    thirdPartyName: '',
    isInsuranceClaims: '',
    isInsured: '',
    casualties: '',
    casualtiesTotal: '',
    damage: '',
    isPowerFailure: '',
    blackoutRange: '',
    equipment: '',
    content: '',
  });
  onMounted(async () => {
    query.id = await getBizParamId();

    await getIndex({ id: query.id }).then((data) => {
      if (query.id) {
        assign(detailFormState, data.result);
        if (
          detailFormState.status == 0 &&
          detailFormState.personMemberId.substring(0, 32) != getUserInfo.userId
        ) {
          detailFormState.status = 10;
        }
      } else {
        assign(detailFormState, query);
        detailFormState.spinning = false;
        detailFormState.status = 0;
        detailFormState.billCode = data.result.billCode;
        detailFormState.personMemberName = data.result.personMemberName;
        detailFormState.fillinDate = data.result.fillinDate;
        detailFormState.deptName = data.result.deptName;
      }

      if (detailFormState.status == 1) {
        disabledInput.value = true;
      }
    });

    if (!query.id) {
      disabledInput.value = false;
    }
    await getRecordable();

    if (
      detailFormState.bizFileId == null ||
      detailFormState.bizFileId == undefined ||
      detailFormState.bizFileId == ''
    ) {
      getBizFileId();
    } else {
      handleAttachmentCreate();
    }
  });

  const getBizParamId = async () => {
    let id = query.id;
    if (!id && query.processInstanceId) {
      const processInstance = await ProcessInstanceApi.getProcessInstance(
        query.processInstanceId as string,
      );
      const businessKey = processInstance.businessKey;
      query.id = businessKey;
      id = businessKey;
    }
    return id;
  };

  const getRecordable = async () => {
    disabledInput.value = detailFormState.status != 0;
    billTitleOption.title = '安全事故快报';
    detailFormState.processInstanceId = query.processInstanceId;
    let { billCode, fillinDate, personMemberName, deptName } = detailFormState;
    assign(billTitleOption, { billCode, fillinDate, personMemberName, deptName });
  };

  //==================================附件资料  begin ==================================

  const getBizFileId = async () => {
    FileApi.getBizId().then((res) => {
      detailFormState.bizFileId = res;
      handleAttachmentCreate();
    });
  };

  const handleAttachmentCreate = async () => {
    initUploadRows(detailFormState.bizFileId);
  };
  const initUploadRows = (bizId: string) => {
    const fileTypes = ['安全事故快报'];
    const fileKindIds = ['safetyAccident'];
    const base = {
      fileKindId: '',
      filetype: '',
      bizId: bizId,
      maxCount: 20,
      maxSize: 20 * 1024 * 1024,
      required: false,
      readonly: true,
    };
    const rows = [] as any;

    for (let i = 0; i < 1; i++) {
      let row = { ...base };
      row.fileKindId = fileKindIds[i];
      row.filetype = fileTypes[i];
      row.readonly = disabledInput.value;
      rows.push(row);
    }

    uploadRows.value = rows;
  };

  const fileList = ref([]);

  const attachmentColumns: BasicColumn[] = [
    {
      title: '附件类型',
      dataIndex: 'fileType',
      align: 'left',
      width: 150,
    },
    {
      title: '附件',
      dataIndex: 'bizFileId',
      align: 'left',
      width: 600,
    },
  ];

  const [registerAttachmentTable, { getDataSource: getAttachmentDataSource }] = useTable({
    columns: attachmentColumns,
    pagination: false,
    showIndexColumn: false,
    showTableSetting: false,
    tableSetting: { fullScreen: true },
    dataSource: fileList,
  });
  //==================================附件资料  end ==================================
</script>
<style lang="less" scoped></style>
